
import React, { useState, useEffect } from 'react';
import { useAuthState } from 'react-firebase-hooks/auth';
import { doc, updateDoc, getDoc } from 'firebase/firestore';
import { auth, db } from '../lib/firebase';
import { Loader2, Save, Phone, User as UserIcon, LogOut } from 'lucide-react';
import { UserProfile } from '../types';

export const SettingsView = () => {
    const [user] = useAuthState(auth);
    const [loading, setLoading] = useState(false);
    const [phoneNumber, setPhoneNumber] = useState('');
    const [displayName, setDisplayName] = useState('');
    const [successMsg, setSuccessMsg] = useState('');
    const [errorMsg, setErrorMsg] = useState('');

    useEffect(() => {
        const fetchProfile = async () => {
            if (!user) return;
            try {
                const userRef = doc(db, 'users', user.uid);
                const snapshot = await getDoc(userRef);
                if (snapshot.exists()) {
                    const data = snapshot.data() as UserProfile;
                    setPhoneNumber(data.phoneNumber || '');
                    setDisplayName(data.displayName || user.displayName || '');
                }
            } catch (err) {
                console.error("Error fetching profile:", err);
            }
        };
        fetchProfile();
    }, [user]);

    const handleSave = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!user) return;
        setSuccessMsg('');
        setErrorMsg('');
        setLoading(true);

        try {
            // Basic validation for E.164
            let formattedPhone = phoneNumber.trim();
            if (formattedPhone && !formattedPhone.startsWith('+')) {
                // Should we auto-add? Maybe enforce user to add it to avoid locale assumptions.
                // For now, let's just warn or simplistic check.
                setErrorMsg("Phone number must start with + (e.g. +14155552671)");
                setLoading(false);
                return;
            }

            const userRef = doc(db, 'users', user.uid);
            await updateDoc(userRef, {
                phoneNumber: formattedPhone,
                displayName: displayName
            });
            setSuccessMsg("Profile updated successfully!");
        } catch (err: any) {
            console.error("Error saving profile:", err);
            setErrorMsg("Failed to save profile. " + err.message);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="flex flex-col h-full w-full bg-slate-50 p-4 overflow-y-auto">
            <div className="max-w-md mx-auto w-full space-y-6">

                <div className="flex flex-col items-center mb-6">
                    <div className="w-20 h-20 rounded-full bg-indigo-100 flex items-center justify-center text-indigo-600 mb-4 shadow-sm">
                        <UserIcon size={40} />
                    </div>
                    <h2 className="text-2xl font-serif text-slate-800">Your Profile</h2>
                    <p className="text-slate-500 text-sm">{user?.email}</p>
                </div>

                <div className="bg-white rounded-xl shadow-sm border border-slate-100 overflow-hidden">
                    <div className="p-4 border-b border-slate-100 bg-slate-50/50">
                        <h3 className="font-medium text-slate-800">Contact Details</h3>
                    </div>

                    <form onSubmit={handleSave} className="p-6 space-y-4">

                        <div className="space-y-2">
                            <label className="text-sm font-medium text-slate-700">Display Name</label>
                            <input
                                type="text"
                                className="w-full px-4 py-2 rounded-lg border border-slate-200 focus:outline-none focus:ring-2 focus:ring-indigo-500/20 focus:border-indigo-500 transition-colors"
                                placeholder="What should we call you?"
                                value={displayName}
                                onChange={(e) => setDisplayName(e.target.value)}
                            />
                        </div>

                        <div className="space-y-2">
                            <label className="text-sm font-medium text-slate-700">WhatsApp Number</label>
                            <div className="relative">
                                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none text-slate-400">
                                    <Phone size={16} />
                                </div>
                                <input
                                    type="text"
                                    className="w-full pl-10 pr-4 py-2 rounded-lg border border-slate-200 focus:outline-none focus:ring-2 focus:ring-indigo-500/20 focus:border-indigo-500 transition-colors font-mono text-sm"
                                    placeholder="+1234567890"
                                    value={phoneNumber}
                                    onChange={(e) => setPhoneNumber(e.target.value)}
                                />
                            </div>
                            <p className="text-xs text-slate-400">
                                Enter your number in E.164 format (e.g. +14155552671). Message this number from WhatsApp to sync.
                            </p>
                        </div>

                        {errorMsg && (
                            <div className="p-3 rounded-lg bg-red-50 text-red-600 text-sm">
                                {errorMsg}
                            </div>
                        )}

                        {successMsg && (
                            <div className="p-3 rounded-lg bg-green-50 text-green-600 text-sm">
                                {successMsg}
                            </div>
                        )}

                        <button
                            type="submit"
                            disabled={loading}
                            className="w-full bg-indigo-600 text-white py-2.5 rounded-lg font-medium hover:bg-indigo-700 active:bg-indigo-800 transition-colors flex items-center justify-center gap-2 disabled:opacity-70 disabled:cursor-not-allowed shadow-sm"
                        >
                            {loading ? <Loader2 className="animate-spin" size={18} /> : <Save size={18} />}
                            Save Changes
                        </button>
                    </form>
                </div>

                <div className="text-center pt-4">
                    <button
                        onClick={() => auth.signOut()}
                        className="text-red-600 text-sm font-medium hover:text-red-700 flex items-center justify-center gap-2 mx-auto px-4 py-2 rounded-lg hover:bg-red-50 transition-colors"
                    >
                        <LogOut size={16} />
                        Sign Out
                    </button>
                </div>

            </div>
        </div>
    );
};
